.investment {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: relative;
  top: 100px;
  margin-bottom: 200px;
}
/* Tables */
.types {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}
.type {
  flex-direction: row;
  max-width: 100vw;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  position: absolute;
  background-color: rgba(154, 170, 184, 0.75);
  z-index: 10;
  padding: 25px 25px 50px 25px;
  margin-bottom: 150px;
  top: -10px;
}
.tables {
  background-color: rgb(242, 243, 244);
  margin: 15px;
  border-radius: 7px;
  box-shadow: 5px 5px 15px rgb(130, 142, 154);
}
table {
  display: flex;
  border: 1.5px solid rgb(65, 79, 159);
  border-radius: 10px;
  width: fit-content;
  margin: 15px;
}
th {
  border-left: 0;
  padding: 5px;
}
td {
  text-align: left;
  padding: 5px;
  max-width: 35vw;
}
.columns {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding: 5px;
}
/*  */
.noInvest ol {
  width: 50vw;
}
.yesInvest ol {
  width: 50vw;
}
.tip {
  border: 5px dotted rgb(65, 79, 159);
  border-radius: 10px;
  padding: 0 10px;
  margin: 10px;
}
